'use client'

import { useState, useEffect } from 'react'
import FormInput from '@/components/inputs/FormInput'
import FormSelect from '@/components/inputs/FormSelect'
import PageWrapper from '@/components/layout/PageWrapper'

const REQUIRED_DOCS = [
  'Account Opening Form',
  'NIN Slip',
  'Undertaken Form',
  'Consent Form',
  'Indemnity Form',
  'Application Letter',
]

export default function ClientApplicationForm() {
  const [form, setForm] = useState({
    firstName: '',
    surname: '',
    otherNames: '',
    pfa: '',
    rsaBalance: '',
    bvn: '',
    nin: '',
    dob: '',
    phone1: '',
    phone2: '',
    altEmail: '',
  })

  const [requiredDocs, setRequiredDocs] = useState<{ [key: string]: File | null }>({})
  const [optionalDocs, setOptionalDocs] = useState([{ title: '', file: null as File | null }])
  const [submitting, setSubmitting] = useState(false)
  const [message, setMessage] = useState('')

  useEffect(() => {
    fetch('http://localhost:5000/api/auth/me', {
      credentials: 'include',
    })
      .then(async (res) => {
        if (!res.ok) throw new Error('Unauthorized')
        const user = await res.json()

        if (user.role !== 'client') {
          window.location.href = '/'
          return
        }

        setForm((prev) => ({
          ...prev,
          firstName: user.first_name || '',
          surname: user.surname || '',
          otherNames: user.other_names || '',
          pfa: user.pfa || '',
          rsaBalance: user.rsa_balance || '',
          bvn: user.bvn || '',
          nin: user.nin || '',
          dob: user.dob || '',
          phone1: user.phone_number || '',
          phone2: user.alt_phone || '',
          altEmail: user.alt_email || '',
        }))
      })
      .catch(() => {
        window.location.href = '/login'
      })
  }, [])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setForm((prev) => ({ ...prev, [name]: value }))
  }

  const handleRequiredDocUpload = (docType: string, file: File | null) => {
    setRequiredDocs((prev) => ({ ...prev, [docType]: file }))
  }

  const handleOptionalDocChange = (index: number, key: 'title' | 'file', value: any) => {
    const docs = [...optionalDocs]
    docs[index][key] = value
    setOptionalDocs(docs)
  }

  const addOptionalDoc = () => {
    setOptionalDocs([...optionalDocs, { title: '', file: null }])
  }

  const handleSubmit = async () => {
    const formData = new FormData()
    Object.entries(form).forEach(([key, value]) => formData.append(key, value))

    REQUIRED_DOCS.forEach((docTitle) => {
      const file = requiredDocs[docTitle]
      if (file) formData.append('documents', file, `${docTitle}`)
    })

    optionalDocs.forEach(({ title, file }) => {
      if (file && title) {
        formData.append('documents', file, `optional:${title}`)
      }
    })

    setSubmitting(true)
    setMessage('')
    try {
      const res = await fetch('http://localhost:5000/api/client/apply', {
        method: 'POST',
        credentials: 'include',
        body: formData,
      })
      if (!res.ok) throw new Error('Submission failed')
      setMessage('✅ Application submitted successfully!')
    } catch (err) {
      setMessage('❌ Could not submit application.')
    } finally {
      setSubmitting(false)
    }
  }

  return (
    <PageWrapper title="Apply for Housing">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <FormInput label="First Name" name="firstName" value={form.firstName} disabled />
        <FormInput label="Surname" name="surname" value={form.surname} disabled />
        <FormInput label="Other Names (Optional)" name="otherNames" value={form.otherNames} disabled />
        <FormSelect label="PFA" name="pfa" value={form.pfa} onChange={handleChange}>
          <option value="">-- Select PFA --</option>
          <option value="ARM Pensions">ARM Pensions</option>
          <option value="Stanbic IBTC">Stanbic IBTC</option>
          <option value="Trustfund Pensions">Trustfund Pensions</option>
        </FormSelect>
        <FormInput label="RSA Balance" name="rsaBalance" value={form.rsaBalance} type="number" onChange={handleChange} />
        <FormInput label="BVN" name="bvn" value={form.bvn} onChange={handleChange} />
        <FormInput label="NIN" name="nin" value={form.nin} onChange={handleChange} />
        <FormInput label="Date of Birth" name="dob" value={form.dob} type="date" onChange={handleChange} />
        <FormInput label="Phone Number" name="phone1" value={form.phone1} onChange={handleChange} />
        <FormInput label="Alt Phone Number" name="phone2" value={form.phone2} onChange={handleChange} />
        <FormInput label="Alt Email" name="altEmail" type="email" value={form.altEmail} onChange={handleChange} />
      </div>

      <hr className="my-6" />
      <h3 className="text-lg font-semibold mb-2">Mandatory Documents</h3>
      <div className="space-y-4">
        {REQUIRED_DOCS.map((doc) => (
          <div key={doc}>
            <label className="block mb-1 text-sm font-medium text-gray-700">{doc}</label>
            <input type="file" required onChange={(e) => handleRequiredDocUpload(doc, e.target.files?.[0] || null)} />
          </div>
        ))}
      </div>

      <hr className="my-6" />
      <h3 className="text-lg font-semibold mb-2">Optional Documents</h3>
      {optionalDocs.map((doc, idx) => (
        <div key={idx} className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-2">
          <input
            type="text"
            placeholder="Document Title"
            className="border p-2 rounded"
            value={doc.title}
            onChange={(e) => handleOptionalDocChange(idx, 'title', e.target.value)}
          />
          <input
            type="file"
            onChange={(e) => handleOptionalDocChange(idx, 'file', e.target.files?.[0] || null)}
          />
        </div>
      ))}
      <button onClick={addOptionalDoc} className="text-blue-600 text-sm mb-4">
        + Add Another Optional Document
      </button>

      <button
        onClick={handleSubmit}
        disabled={submitting}
        className={`w-full py-2 rounded-lg text-white ${submitting ? 'bg-gray-400' : 'bg-blue-600 hover:bg-blue-700'}`}
      >
        {submitting ? 'Submitting...' : 'Submit Application'}
      </button>

      {message && <p className="mt-4 text-sm text-gray-700">{message}</p>}
    </PageWrapper>
  )
}
