'use client'

import { useEffect, useState } from 'react'
import ClientApplicationForm from '@/components/forms/ClientApplicationForm'
import PageWrapper from '@/components/layout/PageWrapper'

export default function ApplyPage() {
  const [userData, setUserData] = useState<any>(null)

  useEffect(() => {
    fetch('http://localhost:5000/api/auth/me', {
      credentials: 'include',
    })
      .then(async (res) => {
        if (!res.ok) throw new Error('Unauthorized')
        const user = await res.json()

        if (user.role !== 'client') {
          window.location.href = '/'
          return
        }

        setUserData(user)
      })
      .catch(() => {
        window.location.href = '/login'
      })
  }, [])

  if (!userData) {
    return (
      <PageWrapper title="Apply">
        <p className="text-center text-gray-600">Loading your data...</p>
      </PageWrapper>
    )
  }

  return (
    <PageWrapper title="Apply for Housing">
      <ClientApplicationForm userData={userData} />
    </PageWrapper>
  )
}
